import * as React from 'react';
import { RenderToolbar, ItemKey } from '@ali/4ever-component';
import createMentionButton from './components/next/mentionButton';

export default function createRenderToolbar(
  locale: Record<string, string>,
): RenderToolbar {

  return (props, controller) => {

    const { buttonName, extraProps } = props;

    if (buttonName === ItemKey.mentions) {
      return createMentionButton({
        controller,
        locale,
        ...extraProps,
      })
    }

    return null
  };
}
